export default {
  methods: {
    getBarberStatusTextByStatusId(statusId) {
      window.console.log('getBarberStatusTextByStatusId')
      const barberStatus = this.$BARBER_STATUS
      let statusText = ''
      switch (statusId) {
        case barberStatus.AGUARDANDO_CADASTRO:
          statusText = 'Aguardando cadastro'
          break;

        case barberStatus.ATIVO:
          statusText = 'Ativo'
          break;

        case barberStatus.BLOQUEADO:
          statusText = 'Bloqueado'
          break;

        default:
          break;
      }
      return statusText
    }
  }
}