const DashboardMixin = {
  computed: {
    barberState() {
      return this.$store.getters['DashboardStore/barber']
    },
    periodState() {
      return this.$store.getters['DashboardStore/period']
    }
  },
  methods: {
    setBarberState(barber) {
      this.$store.dispatch('DashboardStore/setBarber', barber)
    },
    setPeriodState(period) {
      this.$store.dispatch('DashboardStore/setPeriod', period)
    }
  }
}

export default DashboardMixin