<template>
  <dashboard-table
    :headers="headers"
    :items="items"
    :loading="loading"
    :noPagination="true"
    :page="currentPage"
    :qtdPages="pages"
    title="Ranking de barbeiros"
  />
</template>

<script>
import DashboardTable from './DashboardTable'
import BarbershopMixin from '@/mixins/BarbershopMixin'
import BarberStatusMixin from '@/mixins/BarberStatusMixin'
import DashboardMixin from '@/mixins/DashboardMixin';
import TextMixin from '@/mixins/TextMixin'
export default {
  name: 'BarbershopBarberRanking',
  components: { DashboardTable },
  mixins: [ BarbershopMixin, BarberStatusMixin, DashboardMixin, TextMixin ],
  data: () => ({
    currentPage: null,
    headers: [
      { text: '', value: 'image', class: 'text-center', type: 'image' },
      { text: 'Barbeiro', value: 'barber', class: 'text-left' },
      { text: 'Atendimentos', value: 'schedules', class: 'text-center' },
      { text: 'Valor', value: 'revenues', class: 'text-center' },
      { text: 'Status', value: 'status', class: 'text-center' }
    ],
    items: [],
    loading: true,
    pages: 1
  }),
  watch: {
    periodState () {
      this.getBarberRanking()
    }
  },
  mounted () {
    this.getBarberRanking()
  },
  methods: {
    getBarberRanking () {
      let url = `/barbershop/${ this.barbershop.id }/barber-ranking`
      if (this.periodState) {
        if (this.periodState.startDate) url += `startDate=${this.periodState.startDate}&`
        if (this.periodState.endDate) url += `endDate=${this.periodState.endDate}`
      }
      this.$axios(url)
      .then(response => {
        this.loading = false
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            this.items = response.data.corpo.map(item => {
              return {
                image: item.image_url,
                barber: item.name,
                schedules: item.qtd_schedules,
                revenues: `R$ ${ this.setMoneyMask(Number(item.revenues).toFixed(2)) }`,
                status: this.getBarberStatusTextByStatusId(item.barber_status_id)
              }
            })
          } else this.onGetBarberRankingError()
        } else this.onGetBarberRankingError()
      }).catch(() => this.onGetBarberRankingError())
    },
    onGetBarberRankingError () {
      this.loading = false
    }
  }
}
</script>